#p-scoutpad.c-case-study {

	$background-color: #F4F5F5;
	$accent-color: #24323D;
	$border-color: #BABEC1;

	.c-section--with-background {
		background-color: $background-color;
	}
	
	.c-image-cover {
		background-color: $accent-color;
		background-image: url('../../assets/pages/case-studies/scoutpad/cover-1920w@1x.jpg');
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;

		@include media('xs') {
			min-height: 320px;
			max-height: 480px;

			background-image: 
				-webkit-image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-375w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-375w@2x.jpg') 2x,
			);
			background-image: 
				image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-375w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-375w@2x.jpg') 2x,
			);
		}

		@include media('sm') {
			min-height: 480px;
			max-height: 560px;

			background-image: 
				-webkit-image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-640w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-640w@2x.jpg') 2x,
			);
			background-image: 
				image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-640w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-640w@2x.jpg') 2x,
			);
		}

		@include media('md') {
			max-height: 640px;

			background-image: 
				-webkit-image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-960w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-960w@2x.jpg') 2x,
			);
			background-image: 
				image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-960w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-960w@2x.jpg') 2x,
			);
		}

		@include media('lg') {
			max-height: 880px;

			background-image: 
				-webkit-image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-1152w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-1152w@2x.jpg') 2x,
			);
			background-image: 
				image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-1152w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-1152w@2x.jpg') 2x,
			);
		}

		@include media('xl') {
			max-height: 880px;

			background-image: 
				-webkit-image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-1920w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-1920w@2x.jpg') 2x,
			);
			background-image: 
				image-set(
					url('../../assets/pages/case-studies/scoutpad/cover-1920w@1x.jpg') 1x,
					url('../../assets/pages/case-studies/scoutpad/cover-1920w@2x.jpg') 2x,
			);


		}
	}

	.c-quote {
		background-color: $accent-color;

		.c-quote__author {
			border-top-color: rgba(255, 255, 255, 0.1);
		}

		.c-quote__text,
		.c-quote__name {
			color: $text__white-primary;
		}

		.c-quote__position {
			color: $text__white-secondary;
		}
	}

	.l-table, 
	.l-table__head,
	.l-table__row,
	.c-images__head {
		border-bottom-color: $border-color;
	}

	.c-feature__text {
		border-top-color: $border-color;	
	}

	.l-table__head span,
	.c-table__numbered-list__number span,
	.c-images__head,
	.c-feature__text {
		color: $accent-color;
	}
}